@media screen and (min-width: 320px) {
  .container {
    width: 100%;
    min-width: 320px;
    padding: 0px 25px;
  }
}
@media screen and (min-width: 768px) {
  .container {
    min-width: 768px;
  }
}
@media screen and (min-width: 920px) {
  .container {
    min-width: 920px;
  }
}
@media screen and (min-width: 1200px) {
  .container {
    min-width: 1200px;
  }
}
